.product-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 300px;
  height: auto;
  border-radius: 10px;
  border: none;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.1),
              -5px -5px 15px rgba(255, 255, 255, 0.7);
  padding: 10px;
  transition: box-shadow 0.3s ease-in-out;
  cursor: pointer;
}

.product-card:hover {
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2),
              -10px -10px 20px rgba(255, 255, 255, 0.9);
}

.product-image img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.product-name {
  font-size: 18px;
  font-weight: bold;
  margin-top: 10px;
  text-align: center;
}

.product-size {
  font-size: 16px;
  margin-top: 5px;
  text-align: center;
}

.view-details-btn {
  margin-top: 10px;
  padding: 10px 20px;
  border-radius: 20px;
  border-color: #757575;
  background-image: linear-gradient(to bottom, #000000, #000000);
  color: rgb(255, 255, 255);
  transition: transform 0.2s ease-in-out;
  font-size: 16px;
  cursor: pointer;
}

.view-details-btn:hover {
  background-image: linear-gradient(to bottom, #000000, #000000);
  
  color: white;
}

@media screen and (max-width: 768px) {
  .product-card {
    max-width: 95%;
    margin-bottom: 20px;
  }
  .product-cards-container{
    margin-top: 20px;
  }
  .product-image{

      display: flex;
      align-self: center;
      width: 80%;

  }
}